import { PureComponent, ReactElement, lazy, Suspense, Fragment } from 'react';
import { connect } from 'react-redux';
import { Routes, Route } from 'react-router-dom';
import { ToastContainer, Slide } from 'react-toastify';
import { TStoreState } from '../store/config/store';
import Loading from './components/Loading/Loading';

const TokenTable = lazy(() => import('./TokenTable/TokenTable'));
const Swap = lazy(() => import('./Swap/Swap'));

interface IStoreProps {
  isLoading: boolean;
}
interface IHOCProps {}
interface IOwnProps {}
interface IOwnState {}

class App extends PureComponent<
  IStoreProps & IHOCProps & IOwnProps,
  IOwnState
> {
  render(): ReactElement {
    const { isLoading } = this.props;

    return (
      <Fragment>
        <ToastContainer theme={'dark'} transition={Slide} />
        <Loading isShow={isLoading} />
        <Routes>
          <Route
            path='/'
            element={
              <Suspense fallback={<Loading isShow={true} />}>
                <TokenTable />
              </Suspense>
            }
          />
          <Route
            path='/swap/:address'
            element={
              <Suspense fallback={<Loading isShow={true} />}>
                <Swap />
              </Suspense>
            }
          />
          <Route
            path='/swap'
            element={
              <Suspense fallback={<Loading isShow={true} />}>
                <Swap />
              </Suspense>
            }
          />
        </Routes>
      </Fragment>
    );
  }
}

const mapState = (state: TStoreState): IStoreProps => ({
  isLoading: state.loading.isLoading,
});

export default connect(mapState)(App);
