import { handleActions } from 'redux-actions';
import { showLoading, hideLoading } from './loadingActionCreater';

export type TLoading = {
  isLoading: boolean;
};

const initialState: TLoading = {
  isLoading: false,
};

export default handleActions(
  {
    [showLoading as any]: (state: TLoading): TLoading => ({
      ...state,
      isLoading: true,
    }),
    [hideLoading as any]: (state: TLoading): TLoading => ({
      ...state,
      isLoading: false,
    }),
  },
  initialState
);
